body {
    background-color: rgb(250, 250, 250);
}
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.site__logo {
    margin: 1rem 0rem 3rem;
    max-width: 370px;
}

@media (max-width: 770px) {
    .container {
        margin: 0 10%;
    }
    .site__logo {
        width: 100%;
    }
}
.follow__me {
    text-align: center;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 2rem;
    background: #36D4C1;
    padding: 1rem 3rem;
    border-radius: 1rem;
    transition: 650ms ease;
    color: #fff;
    margin: 1rem auto;

}

.follow__me:hover {
    transform: scale(1.1)
}